							h5: '',
							h5y: 'Bank Transfer',
							nr: '',
							txt: [{
								txt1: '1',
								txt2: ' U'
							}, {
								txt1: '2',
								txt2: ''
							}]
						},
						{
							h5: '',
							h5y: 'Pay On Delivery',
							nr: '',
							txt: [{
								txt1: '1. ',
								txt2: '2-7'
							}]
						},
					]
				}
			}
	}
</script>

