				</div>
			</div>
</template>

<script>
	import BMap from 'BMap'
	export default {
		mounted() {
			var map = new BMap.Map("allmap"); // Map
			map.centerAndZoom(new BMap.Point(116.404, 39.915), 11); // ,
			//
			map.addControl(new BMap.MapTypeControl({
				mapTypes: [
					BMAP_NORMAL_MAP,
					BMAP_HYBRID_MAP
				]
			}));
			map.centerAndZoom("",15);  
			map.setCurrentCity("");          // 
			map.enableScrollWheelZoom(true); //
		}
	}
</script>

